.calCon{
	padding: 30px 0;
	background: #fff;
	position: relative;
}
.calCon .fc-toolbar.fc-header-toolbar{
	position: relative;
}
.calCon .fc-left{
	position: absolute;
}
.calCon .fc-button.fc-prev-button, .calCon .fc-button.fc-next-button{
	background: none;
	border: none;
    box-shadow: none;
    margin: 0;
    outline: none;
    color: #333333;
    font-size: 22px;
    position: relative;
    top: -6px;
}
.calCon .fc-center h2{
	font-weight: 400;
    margin: 0;
    font-size: 24px;
    position: relative;
    top: 3px;
    color: #757171;
    width: 100px;
    white-space: nowrap;
    overflow: hidden;
}
.calCon .fc-day-header{
	color: #999;
	font-size: 14px;
    font-weight: 400;
    padding: 15px 10px;	
}
.calCon .fc-ltr .fc-basic-view .fc-day-top .fc-day-number{
	font-size: 20px;
	float: left;
	padding: 10px 20px;
	color: #666;
}
.calCon .fc-left .fc-button-group{
	background: #eaeef4;
	padding: 4px;
    border-radius: 5px;
}
.calCon .fc-left .fc-button-group .fc-button{
	background: none;
    border: none;
    outline: none;
    border-radius: 5px;
    box-shadow: none;
    font-size: 14px;
    text-transform: capitalize;
}
.calCon .fc-left .fc-button-group .fc-button.fc-state-active{
	background: #fff;
	color: #436eee;
}
.calCon .fc-today-button{
	height: 38px;
    background: #eaeef4;
    border: none;
    text-transform: capitalize;
    font-size: 14px;
}
.calCon .fc-today-button.fc-state-disabled{
	opacity: 0.6;
}
.calCon .fc-title{
	color: #fff;
    padding: 2px 4px;
    display: inline-block;
    font-size: 12px;
}
.calCon .fc-unthemed .fc-content, .calCon .fc-unthemed .fc-divider, .calCon .fc-unthemed .fc-list-heading td, .calCon .fc-unthemed .fc-list-view, .calCon .fc-unthemed .fc-popover, .calCon .fc-unthemed .fc-row, .calCon .fc-unthemed tbody, .calCon .fc-unthemed td, .calCon .fc-unthemed th, .calCon .fc-unthemed thead{
	border-color: #efefef;
}
.btn.add-event{
	height: 38px;
    background: #eaeef4;
    border: none;
    text-transform: capitalize;
    font-size: 14px;
    position: absolute;
    right: 0;
    z-index: 10;
    outline: none;
    box-shadow: none;
    color: #333;
}
.fc-license-message{
	display: none;
}